.summary {
	text-align: center;
	max-width: 45rem;
	width: 90%;
	margin: auto;
	margin-top: -10rem;
	position: relative;
	background-color: #383838;
	color: white;
	border-radius: 14px;
	padding: 1rem;
	box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
}

.summary h2 {
	font-size: 2rem;
	margin-top: 0;
}

@media only screen and (max-width: 400px) {
	.summary h2 {
		font-size: 1.5rem;
		margin-top: 0;
	}
	.summary {
		text-align: center;
		max-width: 40rem;
		width: 80%;
		margin: auto;
		margin-top: -10rem;
		position: relative;
		background-color: #383838;
		color: white;
		border-radius: 14px;
		font-size: 15px;
		padding: 1rem;
		box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
	}
}
