.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	width: 100%;
	height: 5rem;
	background-color: #8a2b06;
	color: white;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 0 10%;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
	z-index: 10;
}

.main-image {
	width: 100%;
	height: 27rem;
	z-index: 0;
	overflow: hidden;
}
.main-image {
	position: relative;
	overflow: hidden;
}

.show {
	opacity: 1;
	transition: opacity 1s ease-in-out;
}

.hide {
	opacity: 0;
	transition: opacity 1s ease-in-out;
}
.main-image img {
	width: 110%;
	height: 100%;
	object-fit: cover;
	transform: rotateZ(-5deg) translateY(-4rem) translateX(-1rem);
}

@media only screen and (max-width: 400px) {
	.header {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		width: 100%;
		height: 5rem;
		font-size: 12px;
		background-color: #8a2b06;
		color: white;
		display: flex;
		justify-content: space-evenly;
		align-items: center;
		padding: 0;
		box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
		z-index: 10;
	}
}
