.form {
	margin: 1rem 0;
	height: 19rem;
	overflow: scroll;
}
.form::-webkit-scrollbar {
	display: none;
}

.control {
	margin-bottom: 0.5rem;
}

.control label {
	font-weight: bold;
	margin-bottom: 0.25rem;
	display: block;
	align-items: center;
	text-align: center;
}

.control input {
	font: inherit;
	border: 1px solid #ccc;
	border-radius: 4px;
	width: 30rem;
	max-width: 100%;
	margin: auto;
	display: flex;
}

.actions {
	text-align: center;
	justify-content: flex-end;
	gap: 1rem;
}

.actions button {
	font: inherit;
	color: #5a1a01;
	cursor: pointer;
	background-color: transparent;
	border: none;
	border-radius: 25px;
	padding: 0.5rem 2rem;
	margin-top: 20px;
}

.actions button:hover,
.actions button:active {
	background-color: #ffe6dc;
}

.actions .submit {
	border: 1px solid #5a1a01;
	background-color: #5a1a01;
	color: white;
}
.actions .disable {
	background-color: grey;
	color: black;
	cursor: not-allowed;
}
.actions .submit:hover,
.actions .submit:active {
	background-color: #7a2706;
}

.invalid label {
	color: #ca3e51;
}

.invalid input {
	border-color: #aa0b20;
	background-color: #ffeff1;
}

@media only screen and (max-width: 400px) {
	.form {
		margin: 1rem 0;
		height: 19rem;
		overflow: scroll;
	}
	.form::-webkit-scrollbar {
		display: none;
	}

	.control {
		margin-bottom: 0.5rem;
	}

	.control label {
		font-weight: bold;
		margin-bottom: 0.25rem;
		display: block;
		align-items: center;
	}

	.control input {
		font: inherit;
		border: 1px solid #ccc;
		border-radius: 4px;
		width: 30rem;
		max-width: 100%;
	}
}
