.meal {
	display: flex;
	justify-content: space-between;
	margin: 1rem;
	padding-bottom: 1rem;
	border-bottom: 1px solid #ccc;
}

.meal h3 {
	margin: 0 0 0.25rem 0;
}

.description {
	font-style: italic;
}

.price {
	margin-top: 0.25rem;
	font-weight: bold;
	color: #ad5502;
	font-size: 1.25rem;
}

.form {
	text-align: right;
}

.form button {
	font: inherit;
	cursor: pointer;
	background-color: #8a2b06;
	border: 1px solid #8a2b06;
	color: white;
	padding: 0.25rem 2rem;
	border-radius: 20px;
	font-weight: bold;
}

.form button:hover,
.form button:active {
	background-color: #641e03;
	border-color: #641e03;
}

.meals {
	max-width: 60rem;
	width: 90%;
	margin: 2rem auto;
	animation: meals-appear 1s ease-out forwards;
}

.meals ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

@keyframes meals-appear {
	from {
		opacity: 0;
		transform: translateY(3rem);
	}

	to {
		opacity: 1;
		transform: translateY(0);
	}
}
@media only screen and (max-width: 400px) {
	.meal {
		display: flex;
		justify-content: space-between;
		margin: 0.5rem;
		padding-bottom: 1rem;
		border-bottom: 1px solid #ccc;
	}

	.meal h3 {
		margin: 0 0 0.25rem 0;
	}

	.description {
		font-style: italic;
		font-size: 15px;
	}

	.price {
		margin-top: 0.25rem;
		font-weight: bold;
		color: #ad5502;
		font-size: 1rem;
	}

	.form {
		text-align: right;
	}

	.form button {
		font: inherit;
		cursor: pointer;
		background-color: #8a2b06;
		border: 1px solid #8a2b06;
		color: white;
		padding: 0.25rem 1rem;
		border-radius: 20px;
		font-weight: bold;
	}

	.form button:hover,
	.form button:active {
		background-color: #641e03;
		border-color: #641e03;
	}

	.meals {
		max-width: 60rem;
		width: 90%;
		margin: 2rem auto;
		animation: meals-appear 1s ease-out forwards;
	}

	.meals ul {
		list-style: none;
		margin: 0;
		padding: 0;
	}
}
